@font-face {
  font-family: 'futura';
}

.App {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'futura';
}

.Homepage.container {
  background-color: white;
}

/* 
.Cities.container {
  background-image: url("/images/cities.jpg");
}
 */

.Travelers.container {
  background-color: white;
}

.container {
  position: relative;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
}

.container.no-content {
  -ms-flex-pack: center;
      justify-content: center;
}

.container::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  z-index: 1;
}

.container h1,
.container .content {
  color: red;
  z-index: 2;
}

.container h1 {
  width: 100%;
}

.container .content {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 50px;
}

.options {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 50px;
}

.options .btn {
  display: inline-block;
  width: 250px;
  height: 35px;
  line-height: 30px;
  padding: 5px 8px;
  color: red;
  background-color: white;
  border: 2px solid red;
  -webkit-box-shadow: 5px 5px 8px red;
          box-shadow: 5px 5px 8px red;
  margin: 5px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 20px;
  font-weight: bold;
}

.options .btn:hover {
  background-color: white;
  border: 2px solid red;
  -webkit-box-shadow: inset 8px 8px 5px red;
          box-shadow: inset 8px 8px 5px red;
  -webkit-box-shadow: 10px 10px 10px red;
          box-shadow: 10px 10px 10px red;
}

.options .btn:active {
  background-color: white;
  border: 2px solid red;
  -webkit-box-shadow: inset 4px 4px 8px red;
          box-shadow: inset 4px 4px 8px red;
}

.visited {
  color: #e47cc7;
  text-decoration: line-through;
  font-style: italic;
}

.Cities .towns,
.Travelers .roadtripers {
  font-size: 20px;
  font-weight: bold;
}

.Cities .town,
.Travelers .traveler {
  margin: 10px 0;
}


